body {
  font-family: "Lato", sans-serif;
  text-align: left; }
  body::after {
    clear: both;
    content: "";
    display: table; }
  body h1,
  body h2,
  body h3,
  body p {
    margin: 0; }
  body hr {
    border-bottom: 1px solid #FAFAFA;
    border-left: 0;
    border-right: 0;
    border-top: 0;
    margin: 1.5em 0; }
  body p {
    color: #333;
    line-height: 1.5; }
  body a {
    color: black;
    text-decoration: none; }
  body .type {
    border-bottom: 1px solid;
    display: inline-block;
    font-family: "Lato", sans-serif;
    font-size: 0.7em;
    font-weight: 900;
    letter-spacing: 1px;
    margin-bottom: 2em;
    padding: 0.1em 0;
    text-align: left;
    text-transform: uppercase; }
  body h1 {
    font-family: "Lato", sans-serif;
    font-size: 2em;
    font-weight: 600;
    margin-bottom: 0.6em; }
    @media screen and (min-width: 40em) {
      body h1 {
        font-size: 2.4em; } }
  body h2 {
    font-family: "Lato", sans-serif;
    font-size: 1.2em;
    font-weight: 400;
    line-height: 1.4em;
    margin-bottom: 1.2em; }
    @media screen and (min-width: 40em) {
      body h2 {
        font-size: 1.4em; } }
  body code {
    background: #f7f7f7;
    border-radius: 4.5px;
    border: 1px solid #e0e0e0;
    font-family: monaco;
    font-size: 0.65em;
    font-style: normal;
    padding: 0.1em 0.4em;
    white-space: nowrap; }
  body h3 {
    font-family: "Lato", sans-serif;
    font-size: 1em;
    font-weight: 700;
    line-height: 1.4em;
    margin-bottom: 0.5em;
    text-transform: uppercase; }
  body p.date {
    color: rgba(51, 51, 51, 0.4);
    font-family: "Lato", sans-serif;
    font-size: 0.8em;
    margin-bottom: 0.5em; }
  body p {
    font-family: "Lato", sans-serif;
    font-weight: 300;
    letter-spacing: 1;
    margin-bottom: 1.5em; }
    body p span {
      font-family: "Lato", sans-serif;
      font-size: 0.8em;
      font-weight: 600;
      text-transform: uppercase; }
  body hr {
    width: 3em; }
  body a.read-more {
    display: inline-block;
    font-family: "Lato", sans-serif;
    font-size: 0.8em;
    font-weight: 700;
    margin-left: 0.2em;
    position: relative;
    text-transform: uppercase; }
    body a.read-more span {
      font-size: 1.7em;
      position: absolute;
      right: -10px;
      top: -2px; }
  body p.author {
    font-family: "Lato", sans-serif;
    font-style: italic; }

table {
  border: 1px solid #949494;
  border-collapse: separate;
  border-radius: 3px;
  border-spacing: 0;
  width: 100%; }
  table tbody {
    background-color: #e6e6e6; }
    table tbody tr:hover > td, table tbody tr:hover > th {
      background-color: #d9d9d9; }
  table thead:first-of-type tr:first-child > th:first-child {
    border-top-left-radius: 3px; }
  table thead:first-of-type tr:first-child > th:last-child {
    border-top-right-radius: 3px; }
  table tbody:last-child tr:last-child > td:first-child {
    border-bottom-left-radius: 3px; }
  table tbody:last-child tr:last-child > td:last-child {
    border-bottom-right-radius: 3px; }
  table thead th {
    background-color: white;
    border-bottom: 0;
    border-left: 1px solid #949494;
    padding: 0.75em 1em;
    text-align: left; }
    table thead th:first-child {
      border-left: 0; }
  table tbody {
    background-color: white; }
    table tbody td {
      border-bottom: 0;
      border-left: 1px solid #949494;
      border-top: 1px solid #949494;
      padding: 0.75em 1em; }
      table tbody td:first-child {
        border-left: 0; }
      table tbody td button, table tbody td a.button {
        display: inline-block;
        font-size: 0.7em;
        line-height: 1.5;
        margin-bottom: 0.3em;
        margin-right: 0.5em;
        outline: none;
        padding: 0.3em 1em;
        width: 100%; }
        @media screen and (min-width: 40em) {
          table tbody td button, table tbody td a.button {
            margin-bottom: 0;
            width: auto; } }

body h2 {
  font-size: 1.6em;
  font-weight: 500; }
