.file-upload {
  background-color: #ffffff;
  margin	: 20px;
	display: block;
	position: relative;
}

.file-upload-btn {
  width: 100%;
  margin: 0;
  color: #fff;
  background: #333;
  border: none;
  padding: 10px;
  border-radius: 4px;
  border-bottom: 4px solid #666;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
  outline: none;
  text-transform: uppercase;
  font-weight: 700;
}

.file-upload-btn:hover {
  background: #444;
  color: #ffffff;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
  cursor: pointer;
}

.file-upload-btn:active {
  border: 0;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

.file-upload-content {
  display: none;
  text-align: center;
}

.file-upload-input {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  outline: none;
  opacity: 0;
  cursor: pointer;
}

.file-upload-wrap {
  margin-top: 20px;
  border: 4px dashed #666;
  position: relative;
}

.file-dropping,
.file-upload-wrap:hover {
  background-color: #CCC;
  border: 4px dashed #ffffff;
}

.file-title-wrap {
  padding: 0 15px 15px 15px;
  color: #222;
}

.drag-text {
  text-align: center;
}

.drag-text h3 {
  font-weight: 100;
  text-transform: uppercase;
  color: #222;
  padding: 60px 0;
	font-size: 2em;
}

.file-upload-file {
  max-height: 200px;
  max-width: 200px;
  margin: auto;
  padding: 20px;
}

.remove-file {
  width: 200px;
  margin: 0;
  color: #fff;
  background: #555;
  border: none;
  padding: 10px;
  border-radius: 4px;
  border-bottom: 4px solid #555;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
  outline: none;
  text-transform: uppercase;
  font-weight: 700;
}

.remove-file:hover {
  background: #444;
  color: #ffffff;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
  cursor: pointer;
}

.remove-file:active {
  border: 0;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

